import { Button } from "@mui/material";
import { useContext } from "react";
// import React from "react";
import { useForm } from "react-hook-form";
import api from "../../utils/api";
import { AllContextData } from "../context/context";
import './form.css'


export const Form = ({ handleClose }) => {

    const data = useContext(AllContextData)

    const addNewPostInState = data[3]

    useForm({
        defaultValues: {
            image: 'https://source.unsplash.com/random/?nature',
            title: ' -  !',
            text: ' ,    '
        }
    })
    
    const { register, handleSubmit, formState: { errors } } = useForm({
        mode: 'onBlur',
    });

    const cbSubmit = (data) => {
        api.addNewPost(data).then((newPost) => addNewPostInState(newPost))
        handleClose()
    }

    return (
        <>
            <form onSubmit={handleSubmit(cbSubmit)} className='form'>

                <label className="labelfor"> {errors?.url?.message ? <p className="paragrafor">{errors?.url?.message}</p> : ' URL '}
                    <input className="inputfor"
                        {...register('image', {
                            required: {
                                value: true,
                                message: '   '
                            },
                            minLength: {
                                value: 12,
                                message: 'url        12 '
                            },
                            pattern: {
                                value: /https:\/\//gm,
                                message: 'url     https://'
                            }

                        })}
                        type='text'
                        placeholder="https://source.unsplash.com/random/500%C3%97400/?nature" // https://source.unsplash.com/random/?nature"
      
                    ></input>
                </label>

                <label className="labelfor"> {errors?.head?.message ? <p className="paragrafor">{errors?.head?.message}</p> : '  '}
                    <input className="inputfor"
                        {...register('title', {
                            required: {
                                value: true,
                                message: '   '
                            },
                            minLength: {
                                value: 3,
                                message: '       3 '
                            },
                        })}
                        type='text'
                        placeholder=""

                    ></input>
                </label>

                <label className="labelfor"> {errors?.body?.message ? <p className="paragrafor">{errors?.body?.message}</p> : '  '}
                    <input className="inputfor"
                        {...register('text', {
                            required: {
                                value: true,
                                message: '   '
                            },
                            minLength: {
                                value: 10,
                                message: '       10 '
                            },
                        })}
                        type='text'
                        placeholder=" "

                    ></input>
                </label>

                <Button type="submit" variant="contained" > </Button>

            </form>

        </>
    )


}



// register -   ,       .            . handleSubmit -    ,    .      . fotmState -         .      errors.